"use client";

import React, { useState, useRef, useEffect } from "react";
import dynamic from "next/dynamic";
import { toast } from "react-toastify";

// Dynamically import Quagga so it only loads on the client side.
const Quagga = dynamic(() => import("quagga").then((module) => module.default || module), { ssr: false });

export default function ScanPage() {
  const [useWebcam, setUseWebcam] = useState(false);
  const [scannedCode, setScannedCode] = useState("");
  const [productName, setProductName] = useState("");
  const [scanning, setScanning] = useState(false);
  const videoRef = useRef(null);

  // Effect: Initialize Quagga if webcam scanning is enabled.
  useEffect(() => {
    // If webcam is not enabled, do nothing.
    if (!useWebcam) return;

    // Ensure getUserMedia is supported.
    if (
      !navigator.mediaDevices ||
      typeof navigator.mediaDevices.getUserMedia !== "function"
    ) {
      toast.error("getUserMedia is not supported. Use HTTPS or localhost.");
      return;
    }

    // Dynamically import and initialize Quagga.
    import("quagga")
      .then((module) => {
        const QuaggaModule = module.default || module;
        if (typeof QuaggaModule.init !== "function") {
          toast.error("Quagga failed to load correctly.");
          return;
        }
        QuaggaModule.init(
          {
            inputStream: {
              name: "Live",
              type: "LiveStream",
              target: videoRef.current,
              constraints: {
                width: 640,
                height: 480,
                facingMode: "environment", // Prefer rear camera if available.
              },
            },
            decoder: {
              readers: ["code_128_reader", "ean_reader", "ean_8_reader"],
            },
          },
          (err) => {
            if (err) {
              console.error("Quagga init error:", err);
              toast.error("Error initializing scanner: " + err.message);
              return;
            }
            QuaggaModule.start();
          }
        );

        // Listen for detected barcodes.
        QuaggaModule.onDetected(async (result) => {
          const code = result.codeResult.code;
          // Prevent multiple detections in rapid succession.
          if (scanning || code === scannedCode) return;
          setScanning(true);
          setScannedCode(code);
          // Show the manual product name input prompt
          setProductName(""); // Clear previous product name
          
          // Optional: If you wish, you can automatically set a default name here.
          // For now, we let the user enter it manually.
          
          // Allow scanning again after 3 seconds.
          setTimeout(() => setScanning(false), 3000);
        });
      })
      .catch((error) => {
        console.error("Error loading Quagga:", error);
        toast.error("Failed to load scanner library.");
      });

    // Cleanup: Stop Quagga when component unmounts or when webcam is disabled.
    return () => {
      import("quagga").then((module) => {
        const QuaggaModule = module.default || module;
        if (QuaggaModule && typeof QuaggaModule.stop === "function") {
          QuaggaModule.offDetected && QuaggaModule.offDetected();
          QuaggaModule.stop();
        }
      });
    };
  }, [useWebcam, scanning, scannedCode]);

  return (
    <div className="min-h-screen flex flex-col items-center justify-center p-4 bg-gray-50">
      <h1 className="text-3xl font-bold mb-4">Scan Product</h1>
      <div className="mb-4">
        <label className="mr-2">Use Webcam:</label>
        <input
          type="checkbox"
          checked={useWebcam}
          onChange={(e) => setUseWebcam(e.target.checked)}
        />
      </div>
      {useWebcam ? (
        <div>
          {/* Display the webcam video */}
          <div ref={videoRef} className="w-80 h-60 bg-black" />
          <p className="mt-2">
            Scanned Barcode: <span className="font-mono">{scannedCode}</span>
          </p>
          {/* When a barcode is detected, show an input to manually enter the product name */}
          {scannedCode && productName === "" && (
            <div className="mt-4">
              <input
                type="text"
                placeholder="Enter Product Name"
                value={productName}
                onChange={(e) => setProductName(e.target.value)}
                className="p-2 border rounded"
              />
              <p className="mt-1 text-sm">After entering, the product name will be saved.</p>
            </div>
          )}
          {productName && (
            <p className="mt-2">
              Product Name: <span className="font-semibold">{productName}</span>
            </p>
          )}
        </div>
      ) : (
        // Fallback manual input mode if webcam is not used.
        <div>
          <input
            type="text"
            placeholder="Enter Barcode (Keyboard Wedge Mode)"
            value={scannedCode}
            onChange={(e) => setScannedCode(e.target.value)}
            className="p-2 border rounded mb-2"
          />
          <div className="mb-2">
            <input
              type="text"
              placeholder="Enter Product Name"
              value={productName}
              onChange={(e) => setProductName(e.target.value)}
              className="p-2 border rounded"
            />
          </div>
          <p>Barcode: {scannedCode}</p>
          <p>Product Name: {productName}</p>
        </div>
      )}
    </div>
  );
}
